import React from 'react';

export default function BotChatBubble (props) {
  return (
    <div className='twcd-bot-message' alt='botImg' > 
      <img src='http://res.cloudinary.com/worldclassdev/image/upload/v1525271472/philip.png' alt='' />
      <div className='chat-bubble'>{props.messageText} </div>
      <div className='timestamp'>{props.messageTime}</div>
    </div>
  )
}



