import React from 'react';

export default function UserChatBubble (props) {
  return (
    <div className='twcd-user-message'>
      <div className='timestamp'>{props.messageTime}</div>
      <div className='chat-bubble'>{props.messageText}</div>
      <img src='http://res.cloudinary.com/worldclassdev/image/upload/v1525271472/user.png' alt='userImg' />
    </div>
  )
}


