import React, { Component } from 'react'

import './App.css'
import Sidebar from './components/Sidebar'
import NavBar from './components/NavBar'
import ChatWindow from './components/ChatWindow'
import axios from 'axios'
import qs from 'qs'

class App extends Component {
  constructor (props) {
    super(props)

    this.chatInputHandler = this.chatInputHandler.bind(this)
    this.addMessageToState = this.addMessageToState.bind(this)
    this.sendMessageHandler = this.sendMessageHandler.bind(this)
    // this.getHotelsNearby= this.getHotelsNearby.bind(this)
 

    this.state = {
      userInput: '',
      typing: false,
      location:{
        latitude:'',
        longitude:''
      },
    	messages: [
        {
          sender: 'bot',
          text: 'Hello there!',
          time: '21:20PM'
        },
        {
          sender: 'bot',
          text: `I am ProDroid. How may I assist you today? \\n/ Type --Help to see my list of special commands.`,
          time: '21:30PM'
        },
        {
          sender: 'user',
          text: `I am the user. I'll fucking test you`,
          time: '21:40PM'
        },
        {
          sender: 'user',
          text: `I am the user. I'll fucking test you`,
          time: '21:40PM'
        }
      ]
    }
  }
  //= ======================================== Custom Functions and Event Handler

  chatInputHandler (e) {
    this.setState({
      userInput: e.target.value
    })
  }
  

  // getHotelsNearby (e) {
  //   e.preventDefault();
  //   const APIKEY = 'AIzaSyAifWlsUS3zpy1CgJrzLurRwOX_BF3jkz4'
  //   const URL = `https://maps.googleapis.com/maps/api/place/nearbysearch/json?location=${this.state.location.latitude},${this.state.location.longitude}&radius=50000&type=lodging&RankBy=PROMIENCE&opeNow=true&type=lodging&key=${APIKEY}`
  //   axios.get(URL).then((response) => {
  //     console.log(response)
  //   })

  // }

  // send message to bot and receive response
  sendMessageHandler (e) {
    e.preventDefault()
    const { userInput } = this.state
    var ctime = new Date()

    this.addMessageToState({
      text: userInput,
      sender: 'user',
      time: ctime.getHours() + ':' + ctime.getMinutes()

    })

    this.setState({
      typing: true,
      userInput: ''
    })

    axios.post(`http://old.hng.fun/profiles/worldclassdev/api/chat.php`, qs.stringify({
      chat: userInput
    })).then((response) => {
      const res = response.data
      const message = res
      /* console.log(res);
      console.log(res.message);
      const message = res ? res.message : false;
      console.log(message);AA */
      if (message) {
        this.addMessageToState({
          text: message,
          sender: 'bot',
          time: ctime.getHours() + ':' + ctime.getMinutes()
        }, { typing: false },{userInput:''})
      }
    })
      .catch((error) => {
        console.log(error)
        this.setState({
          typing: false,
          userInput:''
        })
      })
  }

  // Add new message to state
  addMessageToState (message, options = { typing: true }) {
    const { messages } = this.state
    this.setState({
      messages: [
        ...messages,
        message
      ],
      typing: options.typing
    })
  }

  render () {
    return (
      <div id='wrapper' className=''>
        <div id='page-content-wrapper'>
          <Sidebar />
          <div className='container-fluid'>
            <NavBar />
            <ChatWindow messages={this.state.messages} typing={this.state.typing} userInput= {this.state.userInput} sendMessage={this.sendMessageHandler} handleInput={this.chatInputHandler} />

          </div>
        </div>

      </div>
    )
  }
}

export default App



