import React, { Component } from 'react'
import BotChatBubble from './BotChatBubble'
import UserChatBubble from './UserChatBubble'
import Spinner from './Spinner'

export default class ChatWIndow extends Component {
  constructor(props){
    super();
    this.state = {
      location: {
        latitude: '',
        longitude: ''
      }
    }
    this.scrollToBottom = this.scrollToBottom.bind(this);
    }
   
  scrollToBottom = () => {
    this.messagesEnd.scrollIntoView({ behavior: "smooth" });
  }


  componentDidMount() {
    this.scrollToBottom();
  }
  
  componentDidUpdate() {
    this.scrollToBottom();
  }
  render () {
    return (
      <section className='twcd-chat'>
        {this.props.messages.map((message, i) => {
          if (message.sender === 'bot') {
            return <BotChatBubble key={i} messageText={message.text} messageTime={message.time} />
          }
          if (message.sender === 'user') {
            return <UserChatBubble key={i} messageText={message.text} messageTime={message.time} />
          }
        })
        }
        
        {this.props.typing && (
              <BotChatBubble
                messageText={(<Spinner />)} />
            )}
        <div style={{ float:"left", clear: "both" }}
             ref={(el) => { this.messagesEnd = el; }}>
        </div>


        <div className='input-group twcd-chat-input'>
          <form onSubmit={(e) => this.props.sendMessage(e)}>
            <div className='form-group'>
              <div className='input-group p-1'>
                <input onChange={(e) => this.props.handleInput(e)} type='text' className='form-control' placeholder='Enter message here and hit enter' value={this.props.userInput} aria-label="Recipient's username" aria-describedby='basic-addon2' />
                <div className='input-group-append'>
                  <button className='btn btn-outline-success' onClick={(e) => this.props.sendMessage(e)} type="submit" >Send</button>
                </div>
              </div>
            </div>
          </form>
        </div>

      </section>
    )
  }
}



