import React, { Component } from 'react'

export default class NavBar extends Component {
  render() {
    return (
        <div>
            <nav className="navbar navbar-dark fixed-top">
                <div className="nav-brand">
                    My HNGBot
                </div>
                <button className="navbar-toggler" id="menu-toggle" type="button" data-toggle="collapse" data-target="" aria-controls="navbarToggleExternalContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
                </button>
            </nav>
        </div>
    )
  }
}



