import React, { Component } from 'react'

export default class Sidebar extends Component {
  render() {
    return (
        <div id="sidebar-wrapper">
            <section className="twcd-profile">
                    <div className="twcd-profile-header">
                    </div>
                    <img className="mx-auto img-circle img-fluid img-thumbnail" src="./assets/philip.png" alt="profile"></img>
                    <div className="profile-info text-center p-1">
                            <h2>Justine Philip</h2>
                            <h5>Fullstack Javascript Engineer, UI/UX Designer, Data Visualist</h5>
                    </div>
                    <div className="twcd-profile-footer">
                    </div>
            </section>
        </div>
    )
  }
}



